@import "../2_base/2_base";

// Mobile devices
@media (max-width: 767px) {
  .productContainer {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .productPart1ContainerMobile {
    width: 100%;
    height: 25%;
  }
  .productPart1Container {
    display: none;
  }
  .productPart2Container {
    width: 100%;
    height: 75%;
  }
}
// Desktop devices
@media (min-width: 768px) {
  .productContainer {
    max-width: 1000px;
    height: 725px;
    display: flex;
    flex-direction: row;
    margin: auto;
  }
  .productPart1ContainerMobile {
    display: none;
  }
  .productPart1Container {
    width: 49%;
    min-height: 100%;
    overflow: hidden;
  }
  .productPart2Container {
    width: 51%;
    min-height: 100%;
    position: relative;
  }
}

// XL Desktop devices
@media (min-width: 990px) {
  .productContainer {
    -webkit-box-shadow: 0px 0px 34px 7px rgba(240, 240, 240, 0.68);
    -moz-box-shadow: 0px 0px 34px 7px rgba(240, 240, 240, 0.68);
    box-shadow: 0px 0px 34px 7px rgba(240, 240, 240, 0.68);
  }
}
